/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

/* Sass variables */
/* Sass variables */
.text-box {
  border-radius: 6px;
  border: 1px solid #d4d4d4;
  padding: 8px 12px;
  height: 52px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 100%;
  color: #9f9f9f;
  font-family: "Open Sans", arial, sans-serif;
  font-size: 14px; }

.dropdown-box {
  display: block;
  padding: 0;
  border-radius: 6px;
  position: relative;
  border: 1px solid #d4d4d4;
  background-color: white;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none; }

.dropdown-box.is-opened {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  -webkit-box-shadow: 3px 3px 5px 0 #eee;
          box-shadow: 3px 3px 5px 0 #eee; }

.dropdown-box.is-opened .dropdown-list {
  display: block;
  -webkit-box-shadow: 3px 3px 5px 0 #eee;
          box-shadow: 3px 3px 5px 0 #eee; }

.dropdown-header {
  padding-left: 12px;
  padding-right: 12px; }

.dropdown-icon {
  position: absolute;
  top: 40%;
  right: 2%;
  width: 30px;
  height: 10px;
  background-position: 0 -16px;
  background-size: 100%;
  background-repeat: no-repeat;
  background-image: url("assets/icons/dropdown-icon.svg"); }

.is-opened .dropdown-icon {
  background-position: 0 -8px; }

.dropdown-name {
  padding: 18px 0;
  color: #9f9f9f; }

.is-selected .dropdown-name {
  padding: 8px 0 4px 0;
  font-size: 12px; }

.is-selected .dropdown-selected {
  padding: 16px 12px;
  color: #3a4350;
  padding: 4px 0 8px 0;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none; }

.dropdown-list {
  display: none;
  position: absolute;
  z-index: 1;
  left: -1px;
  right: -1px;
  border-left: 1px solid #d4d4d4;
  border-right: 1px solid #d4d4d4;
  border-bottom: 1px solid #d4d4d4;
  border-bottom-left-radius: 6px;
  border-bottom-right-radius: 6px;
  background-color: white; }

.dropdown-list > li {
  padding: 16px 12px;
  border-top: 1px solid #d4d4d4;
  color: #2a2a2a; }

.is-opened .item-selected {
  background-image: url("assets/icons/selected-mark.svg");
  background-repeat: no-repeat;
  padding-left: 40px;
  background-size: 12%;
  background-position: 6px center; }

.dropdown-list .dropdown-list-footer {
  position: relative;
  padding: 20px 12px; }

.dropdown-list .num-of-selected {
  color: #9f9f9f;
  font-size: 12px; }

.dropdown-list .done {
  position: absolute;
  right: 16px;
  top: 16px;
  font-size: 12px;
  border: 1px solid #1d92ed;
  color: #1d92ed;
  padding: 5px 15px;
  border-radius: 6px; }

.dropdown-list .done:hover {
  -webkit-box-shadow: 0 0 4px 0 #1d92ed;
          box-shadow: 0 0 4px 0 #1d92ed; }

